import React, { PureComponent } from 'react';
import * as THREE from 'three';
// import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader.js';
// import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { MTLLoader } from 'three/examples/jsm/loaders/MTLLoader.js';
import { OBJLoader } from 'three/examples/jsm/loaders/OBJLoader.js';
import shark from '../../media/3d/shark/shark.obj';
import sharkTexture from '../../media/3d/shark/shark.jpg';
// import tokyo from '../../media/3d/tokyo/source/Export.fbx';


const styles = {
    wrapper: {
        width: '100%',
        height: '100%',
        position: 'relative',
        pointerEvents: 'none',
    },
}

class PlaygroundPage extends PureComponent {
    constructor(props) {
        super(props);

        this.playground = React.createRef();

        this.scene = new THREE.Scene()
        // this.scene.background = new THREE.Color( 'transparent' );
        // var scene = new THREE.THREE.Scene();
        this.camera = new THREE.PerspectiveCamera( 75, window.innerWidth / window.innerHeight, 0.1, 1000 );

        this.light = new THREE.HemisphereLight(0x3c6586, 0xfaecea, 1);
        this.scene.add(this.light);

        this.renderer = new THREE.WebGLRenderer({ alpha: true });
        this.renderer.setSize( window.innerWidth, window.innerHeight );
        this.renderer.setClearColor( 0x000000, 0 );

        // const geometry = new THREE.BoxGeometry( 1, 1, 1 );
        // const material = new THREE.MeshBasicMaterial( { color: 0xfaecea } );
        // this.cube = new THREE.Mesh( geometry, material );
        // this.scene.add( this.cube );
        function onPregress(data) {
            console.log('onPregress data:', data);
        }

        function onError(data) {
            console.log('onError data:', data);
        }

        const self = this;
        const loader = new OBJLoader();
        loader.load(shark, function(collada) {
            // console.log('.......loaded');
            self.shark = collada;

            collada.scale.set(0.4, 0.4, 0.4);
            // collada.position.y = -50;
            // collada.position.x = 00;
            collada.position.z = -250;
            // collada.position.y = -100;
            const texture = THREE.ImageUtils.loadTexture(sharkTexture);
            console.log('collada:', collada);
            // collada.material.map = texture;
            loader.setMaterials(texture);


            self.scene.add(collada);
        }, onPregress, onError);

        // let tokyoModel;
        // const loader = new GLTFLoader()
        // loader.load(tokyo, function(collada) {
        //     console.log("collada~~");
        //     tokyoModel = collada.scene;
        //     tokyoModel.scale.set(0.3, 0.3, 0.3);
        //     // tokyoModel.position.y = -300;
        //     // tokyoModel.rotation.x = -Math.PI/2;
        //     // tokyoModel.updateMatrix();
        //     self.scene.add(tokyoModel);
        // }, onPregress, onError)
        // const tokyoModel = ne

        this.camera.position.z = 5;
    }

    componentDidMount() {
        this.playground.current.appendChild( this.renderer.domElement );

        this.animate();
    }


    animate() {
    	requestAnimationFrame(() => this.animate());
        // this.cube.rotation.x += 0.01;
        // this.cube.rotation.y += 0.01;
        if(this.shark) {
            this.shark.rotation.x += 0.01;
            this.shark.rotation.y += 0.01;
        }

    	this.renderer.render( this.scene, this.camera );
    }

    render() {
        return (
            <div style={styles.wrapper} ref={this.playground}></div>
        );
    }
}

export default PlaygroundPage;
